* {
    box-sizing: border-box;
}

.a {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

}

h1 {
    text-align: center;
    padding: 12px 30px;
    border: 1.5px solid black;
    width: 43%;
    font-size: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.wrapper {
    widows: 100%;
   justify-content: center;
}

.pokemonWrapper {
    width: 50%;
    margin-right:4%;
     
}

#pokemonSingleCard {
    height: 50%;
    width: 22%;

}


.cardPokemon {
    cursor: pointer;
    width: 27%;
    height: 230px;
    border: 1.5px solid rgba(18, 10, 143,0.3);
    border-radius: 5px;
    margin: 20px 10px 0 10px;

    text-align: center;

}

.cardPokemon p {
    font-weight: 800;
    text-transform: capitalize;
    margin: 7px 0;


}

#loadeMore {
    background-color: blue;
    color: white;
    font-weight: 600;
    width: 93%;
    margin: 30px auto;
    padding: 14px;
    border-radius: 10px;
    border-color: rgba(7, 32, 224, 0.8);
    cursor: pointer;
}

.types {
    width: 90%;
    display: flex;
    margin: 0 auto;
    justify-content: flex-start;
    align-items: center;
}

.types button {
    margin: 0 5px;
    padding: 5px 10px;
    border-radius: 5px;
}



.cardPokemon img {
    width: 90%;
    height: 60%;
    margin-top: 5px;
}




